export const globalMixin = {
  data: () => ({
    apiURL:
      process.env.NODE_ENV === "production"
        ? "/api/uploads"
        : "http://127.0.0.1:8000/uploads",
    modalOpen: false,
    filterKey: null,
    publicPath: process.env.BASE_URL,
    permissionsPath: null,
  }),
  computed: {
    isUserLoggedIn: function () {
      return this.$store.getters.isLoggedIn;
    },
    userPermissions() {
      if (this.$store.getters.user) {
        let role = this.$store.getters.user.role;
        if (role) return role.permissions;
      }
      return {};
    },
    appSettings() {
      let settings = this.$store.state.settings || {};
      return settings.result || {};
    },
    loggedUser() {
      return this.$store.getters.user || {};
    },
    userRole() {
      if (this.loggedUser.role) return this.loggedUser.role.name;
    },
    currentBranch() {
      return this.$store.getters.branch;
    },
  },
  methods: {
    toggleModal() {
      this.$helper.toggleModalOpen();
      this.modalOpen = !this.modalOpen;
    },
    handleEntityImageModify(args = {}) {
      this.$store.dispatch("SetEntityPostData", args).then(() => {
        document.getElementById("global-upload").click();
      });
    },
    hasPermissionTo(permission) {
      if (this.userRole == "ADMINISTRATOR") return true;
      return this.userPermissions.includes(permission);
    },
    handleDeleteAlertModal(args) {
      this.$store.state.recordDeleter.entityName = args.tableName;
      this.$store.state.recordDeleter.url = args.url;
      if (!this.$helper.empty(args.media)) {
        this.$store.state.recordDeleter.media = { ...args.media };
      }
      if (!this.$helper.empty(args.callback)) {
        this.$store.state.recordDeleter.callback = args.callback;
      }
      this.$store.state.deleteAlert = {
        title: "Deleting a record",
        content:
          "Are you sure you want to delete this " +
          args.entityIdentifer +
          ": <b>" +
          args.entity +
          "?</b> <br/> If you continue, there is no way to undo changes you have made.",
        actionButton: "Yes, delete",
        modalOpen: true,
      };
    },
    canView(path) {
      const access =
        this.$helper.getKeyValue(path, this.userPermissions) || "0000";
      return this.userRole == "ADMINISTRATOR" || access[0] == 1;
    },
    canCreate(path) {
      const access =
        this.$helper.getKeyValue(path, this.userPermissions) || "0000";
      return this.userRole == "ADMINISTRATOR" || access[1] == 1;
    },
    canEdit(path) {
      const access =
        this.$helper.getKeyValue(path, this.userPermissions) || "0000";
      return this.userRole == "ADMINISTRATOR" || access[2] == 1;
    },
    canDelete(path) {
      const access =
        this.$helper.getKeyValue(path, this.userPermissions) || "0000";
      return this.userRole == "ADMINISTRATOR" || access[3] == 1;
    },
  },
};
